import * as React from 'react'
import i18n from '../../i18n'
const { useState, useEffect, useRef } = React

interface IProps {
  timeStamp: any
  returnClick: () => void
}

const CountDown = (props: IProps) => {
  const { timeStamp, returnClick } = props
  const intervalRef = useRef<any>(null)

  const now: any = Math.round(new Date().getTime() / 1000).toString() //
  const end: any = timeStamp //

  const [leftTime, setLeftTime] = useState(end - now) //
  const [h, setHours] = useState<any>('') //
  const [m, setMinutes] = useState<any>('') //
  const [s, setSeconds] = useState<any>('') //
  const [d, setDays] = useState<any>('') // 

  useEffect(() => {
    if (leftTime === 0) returnClick()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [leftTime])

  useEffect(() => {
    if (leftTime > 0) {
      intervalRef.current = setInterval(() => {
        const newNow: any = Math.round(new Date().getTime() / 1000).toString() // 

        let newLeftTime = timeStamp - newNow
        setLeftTime(() => newLeftTime) //

        let day = newLeftTime > 86400 ? Math.floor(newLeftTime / 86400) : '0'

        let hours =
          Math.floor((newLeftTime / 60 / 60) % 24) < 10
            ? `0${Math.floor((newLeftTime / 60 / 60) % 24)}`
            : Math.floor((newLeftTime / 60 / 60) % 24)
        let minutes =
          Math.floor((newLeftTime / 60) % 60) < 10
            ? `0${Math.floor((newLeftTime / 60) % 60)}`
            : Math.floor((newLeftTime / 60) % 60)
        let seconds =
          Math.floor(newLeftTime % 60) < 10 ? `0${Math.floor(newLeftTime % 60)}` : Math.floor(newLeftTime % 60)
        setHours(() => hours) // 
        setMinutes(() => minutes) // 
        setSeconds(() => seconds) //setIntervaluseEffectbug
        setDays(() => day)
      }, 1000)
    } else {
      setLeftTime(0)
      setHours(0)
      setMinutes(0)
      setSeconds(0)
      setDays(0)
      clearInterval(intervalRef.current)
    }
    return () => clearInterval(intervalRef.current)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []) //

  return (
    <>
      {leftTime <= 0 && <span>-</span>}
      {leftTime > 0 && (
        <span>
          {d > 0 ? (i18n.language === 'zh' ? `${d}  ${h}:${m}:${s}` : `${d} Day ${h}:${m}:${s}`) : `${h}:${m}:${s}`}
        </span>
      )}
    </>
  )
}

export default CountDown
