export const Arabia_To_SimplifiedChinese = (Num: any) => {
  for (let i = Num.length - 1; i >= 0; i--) {
    Num = Num.replace(',', '') //Num,
    Num = Num.replace(' ', '') //Num
  }
  if (isNaN(Num)) {
    //
    //alert("");
    return
  }
  //
  let part = String(Num).split('.')
  let newchar = ''
  //
  for (let i = part[0].length - 1; i >= 0; i--) {
    if (part[0].length > 10) {
      //alert("");
      return ''
    } //
    let tmpnewchar = ''
    let perchar = part[0].charAt(i) as any
    switch (perchar) {
      case '0':
        tmpnewchar = '' + tmpnewchar
        break
      case '1':
        tmpnewchar = '' + tmpnewchar
        break
      case '2':
        tmpnewchar = '' + tmpnewchar
        break
      case '3':
        tmpnewchar = '' + tmpnewchar
        break
      case '4':
        tmpnewchar = '' + tmpnewchar
        break
      case '5':
        tmpnewchar = '' + tmpnewchar
        break
      case '6':
        tmpnewchar = '' + tmpnewchar
        break
      case '7':
        tmpnewchar = '' + tmpnewchar
        break
      case '8':
        tmpnewchar = '' + tmpnewchar
        break
      case '9':
        tmpnewchar = '' + tmpnewchar
        break
    }
    switch (part[0].length - i - 1) {
      case 0:
        tmpnewchar = tmpnewchar
        break
      case 1:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 2:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 3:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 4:
        tmpnewchar = tmpnewchar + ''
        break
      case 5:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 6:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 7:
        if (perchar != 0) tmpnewchar = tmpnewchar + ''
        break
      case 8:
        tmpnewchar = tmpnewchar + ''
        break
      case 9:
        tmpnewchar = tmpnewchar + ''
        break
    }
    newchar = tmpnewchar + newchar
  }
  //
  while (
    newchar.search('') != -1 ||
    newchar.search('') != -1 ||
    newchar.search('') != -1 ||
    newchar.search('') != -1
  ) {
    newchar = newchar.replace('', '')
    newchar = newchar.replace('', '')
    newchar = newchar.replace('', '')
    newchar = newchar.replace('', '')
  }
  //
  if (newchar.indexOf('') == 0) {
    newchar = newchar.substr(1)
  }
  //
  if (newchar.lastIndexOf('') == newchar.length - 1) {
    newchar = newchar.substr(0, newchar.length - 1)
  }
  return newchar
}
